body {
  font-family: sans-serif;
  background: #dedede;
  padding: 48px;
}

/* h1 {
  text-align: center;
  display: flex;
  font-size: 50px;
} */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html {
  scroll-behavior: smooth;
}
header {
  margin: 30px 100px 0 105px;
}
.header {
  margin: 0 100px 0 100px;
  display: flex;
  justify-content: space-between;
}
nav {
  display: flex;
}
nav h4 {
  margin: 0 30px 0 12px;
  color: #white;
  font-size: 12px;
  font-weight: 400;
  letter-spacing: 2px;
  text-transform: uppercase;
}
.logo {
  margin: 0 20px 0 10px;
  width: 60.08px;
  height: 30px;
}
.capitalize {
  text-transform: capitalize;
}
.uppercase {
  text-transform: uppercase;
}
.blue {
  color: #5fb4a2;
}
.bio {
  margin: 150px 830px 0 105px;
}
/* ul {
  margin-bottom: 40px;
  color: #33323d;
  font-family: Ibarra Real Nova;
  font-size: 20px;
  font-style: normal;
  font-weight: 560;
  line-height: 80px; /* 100% */
/* letter-spacing: -0.226px; */
/* }  */

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  /* background-color: #33323d;  */
}

li a {
  display: block;
  color: #33323d;
  text-align: center;
  padding: 14px 30px;
  text-decoration: none;
}

li {
  float: left;
}

li a:hover {
  background-color: #5fb4a2;
}
.about-section {
  display: flex;
  width: 100%;
  /* margin: 0 164px 0 165px; */
}
.author-image {
  max-width: 100%;
  height: 676px;
}
h2 {
  text-transform: capitalize;
  margin: 20px 0 10px 0;
}
p {
  color: #33323d;
  font-family: Public Sans;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 30px;
}
.about {
  display: flex;
  margin-bottom: 116px;
}
.about-me {
  margin: 0 20px 0 10px;
  padding: 0 65px;
}
.text {
  color: #fff;
  border: 3px solid #203a4c;
  background-color: #203a4c;
  text-transform: uppercase;
  padding: 5px 5px 0 10px;
}
.arrow {
  background-color: #203a4c;
}
.footer {
  background-color: #33323d;
  color: white;
  display: flex;
  justify-content: space-evenly;
  padding: 33px 94px 33px 92px;
}
/* .footer {
  color: #fff;
} */
.social {
  margin: 0 14px 0 12px;
}
.interest {
  display: flex;
  margin: 230px 0 230px 165px;
}
.interest h3 {
  color: #33323d;
  font-family: Ibarra Real Nova;
  font-size: 40px;
  font-weight: 700;
  line-height: 42px;
  letter-spacing: -0.357px;
  width: 350px;
}
.hr {
  width: 534px;
  height: 1px;
  margin-top: 48px;
}
button {
  margin-top: 1.5rem;
  color: #33323d;
  width: 202px;
  height: 48px;
  text-align: center;
  text-transform: uppercase;
}
@media only screen and (max-width: 414px) and (min-width: 280px) {
  header {
    display: inline;
    margin: 64px 0 0 20px;
  }
  .header {
    justify-content: space-between;
  }
  .bio {
    margin: 98px 0 0 20px;
  }
  .about {
    margin-bottom: 60px;
  }
  .about-section {
    flex-direction: column;
    margin: 0 0 0 20px;
  }
  .author-image {
    max-width: 50%;
    height: 50%;
  }
  .interest {
    display: flex;
    flex-direction: column;
    margin: 20px 0 0 20px;
  }
  .interest h3 {
    font-size: 25px;
    font-weight: 700;
    line-height: 42px;
    letter-spacing: -0.357px;
    width: 100%;
  }
  .social {
    margin: 20px 14px 0 60px;
  }
  .footer {
    flex-direction: row;
    width: 100%;
    flex-wrap: column;
  }
  .footer h4 {
    margin: 20px 0 0 60px;
  }
  button {
    width: 150px;
    margin: 20px 0 20px 100px;
    justify-content: center;
  }
}
